import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  linkActiveClass: "active", //将类名route-link-active 简化成类名active
  linkExactActiveClass: "exact",//route-link-exact-active -> exact
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      name: 'login',
      component: () => import('@/page/login.vue')
    },
    {
      path: '/register',
      name: 'register',
      component: () => import('@/page/register.vue')
    },
    {
      path: '/home',
      name: 'home',
      component: () => import('@/page/home.vue'),
      redirect: '/home/dynamic',
      children: [
        {
          path: 'dynamic',
          name: 'dynamic',
          component: () => import('@/page/dynamic.vue')
        },
        {
          path: 'personal',
          name: 'personal',
          component: () => import('@/page/personal.vue')
        },
        {
          path: 'schoolMate',
          name: 'schoolMate',
          component: () => import('@/page/schoolMate.vue'),
          redirect: 'schoolMate/class/1',
          children: [
            {
              path: 'class/:classID',
              name: 'class',
              component: () => import('@/components/schoolMate/ClassDetail.vue'),
            },
            {
              path: 'class/:classID/album',
              name: 'classalbum',
              component: () => import('@/components/schoolMate/ClassAlbumShow.vue'),
            },
          ]
        },
        {
          path: 'chat',
          name: 'chat',
          component: () => import('@/page/chat.vue')
        },
        {
          path: 'notice',
          name: 'notice',
          component: () => import('@/page/notice.vue')
        }
      ]
    }
  ]
})



